import React, { Component } from 'react'
import { withRouter } from "react-router-dom";
import { List, Card, Pagination, notification, Button, Modal } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { getDevList, getAllDevList, delDev } from "../../api/request"
import "../ListDev/ListDev.css"
@withRouter
class DltDev extends Component {
    constructor() {
        super()
        this.state = {
            data: [],
            total: ""
        }
    }
    //
    openNotificationWithIcon = (type, msg) => {
        notification[type]({
            message: msg
        });
    };
    componentDidMount() {
        this.getDevData()//,  
        getAllDevList().then(res => {
            if (res.data.state) {
                //  
                this.setState({
                    total: res.data.data.length / 5 * 10
                })
            } else {
                //,
                this.openNotificationWithIcon('error', ",!")
            }
        }).catch(err => {
            //
            this.openNotificationWithIcon('error', "")
        })
    }
    getDevData(query = {}) {
        getDevList(query).then(res => {
            if (res.data.state) {
                //
                let data = res.data.findResult.map(item => {
                    return {
                        devName: item.devName,
                        devNum: item.devNum,
                        devDate: item.devDate,
                        devSIM: item.devSIM,
                        devUse: item.devUse,
                        devAdmin: item.devAdmin,
                        cTime: item.cTime,
                        devLocation: item.devLocation.join(",")
                    }
                })
                this.setState({
                    data
                })
            } else {
                //,
                this.openNotificationWithIcon('error', ",!")
            }
        }).catch(err => {
            //
            this.openNotificationWithIcon('error', "")
        })
    }
    //
    changePage = (page) => {
        this.getDevData({ page })
    }
    //
    aclick(item) {
        Modal.confirm({
            title: '',
            icon: <ExclamationCircleOutlined />,
            content: "" +item.devNum+""+item.devName,
            okText: '',
            cancelText: '',
            onOk:()=>{
                delDev(item.devNum).then(res=>{
                    console.log(res);
                    if(res.data.state){
                        //
                        this.openNotificationWithIcon('success', item.devName+"")
                    } else if (res.data.msg === "()"){//------
                        //()
                        this.openNotificationWithIcon('error', "!!!!")
                        sessionStorage.clear()
                        this.props.history.push("/login")
                    } else {
                        //,
                        this.openNotificationWithIcon('error', ",!")
                    }
                }).catch(err=>{
                    //
                    this.openNotificationWithIcon('error', "")
                })
            }
        });
    }
    //
    goMap(location, name) {
        console.log(location);
        console.log(this.props);
        this.props.history.push("/home/map/" + location + "&" + name)
    }
    render() {
        return (
            <div>
                <List
                    itemLayout="horizontal"
                    dataSource={this.state.data}
                    renderItem={item => (
                        <List.Item>
                            <Card title={<div><span style={{ marginRight: "15px" }}>:{item.devNum}</span><span>:{item.devName}</span></div>} extra={<Button type="primary" danger onClick={this.aclick.bind(this, item)}></Button>} style={{ width: "100%" }} >
                                <List.Item.Meta
                                    description={
                                        <div className="devListStyle">
                                            <p><span>:</span>{item.devDate}</p>
                                            <p><span>SIM:</span>{item.devSIM}</p>
                                            <p><span>:</span>{item.devUse ? "" : ""}</p>
                                            <div className="locationStyle"><span>:</span>{item.devUse ? item.devLocation ? <div>[{item.devLocation}]<a onClick={this.goMap.bind(this, item.devLocation, item.devName)}></a></div> : "" : ","}</div>
                                            <p><span>:</span>{item.devAdmin}</p>
                                            <p><span>:</span>{item.cTime}</p>
                                        </div>
                                    }
                                />
                            </Card>
                        </List.Item>
                    )}
                />
                <Pagination defaultCurrent={1} total={this.state.total} onChange={this.changePage} />
            </div>
        )
    }
}
export default DltDev